import React, { useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";
import { single_product_url as url } from "../utils/constants";
import { useHomeProductsContext } from "../context/Home_Context";
import { useCartProductsContext } from "../context/Cart_Context";
import { Loading, NetWorkError } from "../components";

const OverView = React.memo(() => {
  const { id } = useParams();
  const history = useHistory();
  const {
    single_product_error: error,
    single_product_loading: loading,
    singleProduct: product,
    fetchSingleProduct
  } = useHomeProductsContext();
  //console.log(product);
  const {
    handleCart,
    cartButton,
    cartAniButton,
    cartProduct
  } = useCartProductsContext();

  useEffect(() => {
    fetchSingleProduct(`${url}/${id}`);
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (error) {
      setTimeout(() => {
        history.push("/");
      }, 3000);
    }
    // eslint-disable-next-line
  }, [error]);

  if (loading) {
    return <Loading />;
  }
  if (error) {
    return <NetWorkError />;
  }
  if (cartAniButton === true) {
    setTimeout(() => {
      history.push("/cart");
    }, 1000);
  }
  const cartItem = cartProduct.find((item) => item.keyid === id);

  const { name, price, description, image, _id } = product;
  return (
    <React.Fragment>
      <div style={{ minHeight: "500px" }}>
        <div key={_id}>
          <img
            className=""
            src={`/${image}`}
            //src={`../${image}`}
            width="250px"
            heigth="250px"
            alt={image}
          />
          <p>{name}</p>
          <p>{price}</p>
          <p>{description}</p>
        </div>
        <div>
          <button>buy</button>
          &nbsp;
          {cartItem ? (
            <button onClick={() => history.push("/cart")}>go to cart</button>
          ) : (
            <>
              {cartButton && (
                <button onClick={() => handleCart(product)}>cart</button>
              )}
            </>
          )}
          {cartAniButton && <button>carting...</button>}
        </div>
      </div>
    </React.Fragment>
  );
});

export default OverView;
