import React from "react";
import { useCartProductsContext } from "../context/Cart_Context";
import { Loading, NetWorkError, CartModal,CartSearch,NoSearch } from "../components";
import "./styles/Cart.css";

const Cart = () => {

  
  const {
    cartProduct,
    cart_product_loading: loading,
    cart_product_error: error,
    ModalOpen,
    search

  } = useCartProductsContext();

  const cart= cartProduct.filter((item)=> item.name.toLowerCase().includes(search.toLowerCase()));

  if (loading) {
    return <Loading />;
  }
  if (error) {
    return <NetWorkError />;
  }
 

  return (
    <React.Fragment>
      <CartSearch/>
      {cart.length===0 && 
      <NoSearch/>
      }
  
      {cartProduct && 
      
       <div style={{ minHeight: "500px" }}>
        {cart.map((item) => {
          return (
            <div key={item._id}>
              <img
                src={item.image}
                alt={item.name}
                height="250px"
                width="250px"
              />
              <p>{item.name}</p>
              <p>{item.price}</p>
              <button onClick={() => ModalOpen(item._id)}>delete</button>
              <CartModal />
            </div>
          );
        })}
           {cartProduct.length===0 && !loading ?
           
           <div>No Cart Items</div>
          :
          null
          }
      </div>
      }
    
     
    </React.Fragment>
  );
};

export default Cart;
