import React from "react";
import "./styles/CartModal.css";
import { useCartProductsContext } from "../context/Cart_Context";

const CartModal = () => {
  const { deleteCart, isModalOpen, ModalClose } = useCartProductsContext();

  return (
    <React.Fragment>
      <div
        className={`${
          isModalOpen ? "modal-overlay show-modal" : "modal-overlay"
        }`}
      >
        <div className="modal-container">
          <h3>Are you sure?</h3>
          <button className="close-modal-btn" onClick={ModalClose}>
            X
          </button>
          <div>
          <button onClick={deleteCart}>Yes</button>
          &nbsp;
          <button onClick={ModalClose}>No</button>
          </div>
        
        </div>
      </div>
    </React.Fragment>
  );
};
export default CartModal;
