import React from "react";
import Slider from "react-slick";
import "./styles/Carousel.css";
import "slick-carousel/slick/slick-theme.css";
import "slick-carousel/slick/slick.css";

const Carousel = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    lazyLoad: true,
    centerMode: false,
    adaptiveHeight: true,
    fade: true,
    arrows: true,
    autoplaySpeed: 2000,
    className: "slides"
  };
  return (
    <>
      <Slider {...settings}>
        <div>
          <img
            src="https://rukminim1.flixcart.com/flap/1688/280/image/1c509497b712a280.jpg?q=50"
            alt="Girl in a jacket"
            width="100%"
            height="250"
          />
        </div>
        <div>
          <img
            src="https://citybook.pk/wp-content/uploads/2018/02/sb05-RA-preview.png"
            alt="Girl in a jacket"
            width="100%"
            height="250"
          />
        </div>
        <div>
          <img
            src="https://i.pinimg.com/originals/37/e9/e0/37e9e012b6e0f2a3edf47438f066958d.jpg"
            alt="Girl in a jacket"
            width="100%"
            height="250"
          />
        </div>
        <div>
          <img
            src="https://www.imnotbald.com/wp-content/uploads/2021/05/Online-Shopping-Banner-Mobile-App-Vector.jpg"
            alt="Girl in a jacket"
            width="100%"
            height="250"
          />
        </div>
        <div>
          <img
            src="https://i.pinimg.com/736x/82/d1/a7/82d1a7de8b4ace8d0fb3309117cec204.jpg"
            alt="Girl in a jacket"
            width="100%"
            height="250"
          />
        </div>
      </Slider>
    </>
  );
};

export default Carousel;
