import React from "react";
import { useCartProductsContext } from "../context/Cart_Context";

/*   useRef    */
// 1. preserve value
// 2. Does Not trigger re-render
// 3. Target DOM nodes/elements

const CartSearch = () => {
    const {
        SearchCart,
        refContiner
      } = useCartProductsContext();
      
  return(
      <React.Fragment>
          <div style={{textAlign:"center"}}>

              <form>
                  <div>
                  <input type="text" 
                  ref={refContiner} 
                  onChange={SearchCart} 
                  placeholder="Search.." />
                  </div>
              </form>

          </div>
      </React.Fragment>
  )
};

export default CartSearch;
