import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Error, Navbar, Footer } from "./components";
import { Home, Cart, OverView } from "./pages";

function App() {
  return (
    <Router>
      <Navbar />
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route exact path="/OverView/:id/:name" children={<OverView />} />

        <Route exact path="/cart">
          <Cart />
        </Route>
        <Route path="*">
          <Error />
        </Route>
      </Switch>
      <Footer />
    </Router>
  );
}

export default App;
