import {
  GET_HOME_PRODUCTS_BEGIN,
  GET_HOME_PRODUCTS_SUCCESS,
  GET_HOME_PRODUCTS_ERROR,
  GET_SINGLE_PRODUCT_BEGIN,
  GET_SINGLE_PRODUCT_SUCCESS,
  GET_SINGLE_PRODUCT_ERROR,

} from "../actions";

const Home_Reducers = (state, action) => {
  // homefetch reducers
  if (action.type === GET_HOME_PRODUCTS_BEGIN) {
    return { ...state, home_products_loading: true };
  }
  if (action.type === GET_HOME_PRODUCTS_SUCCESS) {
    return {
      ...state,
      home_products_loading: false,
      HomeProducts: action.payload
    };
  }
  if (action.type === GET_HOME_PRODUCTS_ERROR) {
    return {
      ...state,
      home_products_loading: false,
      home_products_error: true
    };
  }
  //end

  //single Products reducers
  if (action.type === GET_SINGLE_PRODUCT_BEGIN) {
    return {
      ...state,
      single_product_loading: true,
      single_product_error: false
    };
  }
  if (action.type === GET_SINGLE_PRODUCT_SUCCESS) {
    return {
      ...state,
      single_product_loading: false,
      singleProduct: action.payload
    };
  }
  if (action.type === GET_SINGLE_PRODUCT_ERROR) {
    return {
      ...state,
      single_product_loading: false,
      single_product_error: true
    };
  }
  // end

 

  throw new Error(`No Matching "${action.type}" - action type`);
};

export default Home_Reducers;
