import React from "react";
import { useHomeProductsContext } from "../context/Home_Context";
import { useCartProductsContext } from "../context/Cart_Context";
import { Link } from "react-router-dom";

const Navbar = () => {
  const {
    home_products_loading: loading,
    home_products_error: error,
    HomeProducts
  } = useHomeProductsContext();
  const { cartProduct } = useCartProductsContext();
  return (
    <React.Fragment>
      <nav className="navbar navbar-expand-lg  navbar-dark bg-primary">
        <a className="navbar-brand" href="#">
          Navbar
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item active">
              <Link className="nav-link" to="/">
                Home
              </Link>
            </li>

            <li className="nav-item">
              <Link className="nav-link" to="/cart">
                Cart({cartProduct.length})
              </Link>
            </li>
          </ul>

          <ul className="nav navbar-nav navbar-right">
            <li className="nav-item ">
              <a className="nav-link active" href="#">
                Sign Up
              </a>
            </li>
            <li className="nav-item ">
              <a className="nav-link active" href="#">
                Login
              </a>
            </li>
          </ul>
        </div>
      </nav>
    </React.Fragment>
  );
};

export default Navbar;
