import axios from "axios";
import React, { useContext, useEffect, useReducer, useState } from "react";
import reducer from "../reducers/Home_Reducers";
import { home_products_url as url } from "../utils/constants";
import HomePagination from ".././components/HomePagination";

import {
  GET_HOME_PRODUCTS_BEGIN,
  GET_HOME_PRODUCTS_SUCCESS,
  GET_HOME_PRODUCTS_ERROR,
  GET_SINGLE_PRODUCT_BEGIN,
  GET_SINGLE_PRODUCT_SUCCESS,
  GET_SINGLE_PRODUCT_ERROR,

} from "../actions";

const initialState = {
  home_products_loading: false,
  home_products_error: false,
  HomeProducts: [],

  single_product_loading: false,
  single_product_error: false,
  singleProduct: {}
};

const HomeProductsContext = React.createContext();

export const HomeProductsProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const [products, setProducts] = useState([]);

  const fetchProducts = async (url) => {
    dispatch({ type: GET_HOME_PRODUCTS_BEGIN });
    try {
      const response = await axios.get(url);
      const products = response.data;
      setProducts(HomePagination(products));
      dispatch({ type: GET_HOME_PRODUCTS_SUCCESS, payload: products });
    } catch (error) {
      dispatch({ type: GET_HOME_PRODUCTS_ERROR });
    }
  };
  // OverView product fetheing
  const fetchSingleProduct = async (url) => {
    dispatch({ type: GET_SINGLE_PRODUCT_BEGIN });
    try {
      const response = await axios.get(url);
      const singleproduct = response.data;
      dispatch({ type: GET_SINGLE_PRODUCT_SUCCESS, payload: singleproduct });
    } catch (error) {
      dispatch({ type: GET_SINGLE_PRODUCT_ERROR });
    }
  };

  

  useEffect(() => {
    fetchProducts(url);
  }, []);

  return (
    <HomeProductsContext.Provider
      value={{ ...state, fetchSingleProduct, products }}
    >
      {children}
    </HomeProductsContext.Provider>
  );
};
// make sure use
export const useHomeProductsContext = () => {
  return useContext(HomeProductsContext);
};
