import React, { useState, useEffect } from "react";
import { useHomeProductsContext } from "../context/Home_Context";
import { Loading, NetWorkError, Carousel } from "../components";
import { Link } from "react-router-dom";

const Home = React.memo(() => {
  const {
    home_products_loading: loading,
    home_products_error: error,
    HomeProducts,
    products
  } = useHomeProductsContext();
  //console.log(useHomeProductsContext())

  const [page, setPage] = useState(0);
  const [followers, setFollowers] = useState([]);

  useEffect(() => {
    if (loading) return;
    setFollowers(products[page]);
  }, [loading, page]);
  //console.log(products[3])

  if (loading) {
    return <Loading />;
  }
  if (error) {
    return <NetWorkError />;
  }

  const handlePage = (index) => {
    setPage(index);
  };

  const nextPage = () => {
    setPage((oldPage) => {
      let nextPage = oldPage + 1;
      if (nextPage > products.length - 1) {
        nextPage = 0;
      }
      return nextPage;
    });
  };
  const prevPage = () => {
    setPage((oldPage) => {
      let prevPage = oldPage - 1;
      if (prevPage < 0) {
        prevPage = products.length - 1;
      }
      return prevPage;
    });
  };
  return (
    <React.Fragment>
      <div className="App" style={{ minHeight: "500px" }}>
        <Carousel />
        &nbsp;
        {followers &&
          followers.map((item, index) => {
            return (
              <div key={item._id}>
                <Link to={`/OverView/${item._id}/${item.name}`} className="">
                  <img
                    src={item.image}
                    height="250px"
                    width="250px"
                    alt={item.image}
                  />
                </Link>
                <div>{item.name}</div>
                <p>{item.price}</p>

                <hr />
              </div>
            );
          })}
        <div>
          {!loading && (
            <div>
              <button onClick={prevPage}>prev</button>
              {products &&
                products.map((item, index) => {
                  return (
                    <button key={index} onClick={() => handlePage(index)}>
                      {index + 1}
                      {/*console.log(followers)*/}
                    </button>
                  );
                })}
              <button onClick={nextPage}>next</button>
            </div>
          )}
        </div>
      </div>
    </React.Fragment>
  );
});

export default Home;
