import {
  GET_CART_PRODUCTS_BEGIN,
  GET_CART_PRODUCTS_SUCCESS,
  GET_CART_PRODUCTS_ERROR,
  REMOVE_CART,
  HIDE_BUTTON,
  DEFAULT_STATE,
  MODAL_OPEN

} from "../actions";

const Cart_Reducers = (state, action) => {
  // cartfetch reducers
  if (action.type === GET_CART_PRODUCTS_BEGIN) {
    return { ...state, cart_product_loading: true };
  }
  if (action.type === GET_CART_PRODUCTS_SUCCESS) {
    
    return {
      ...state,
      cart_product_loading: false,
      cartProduct: action.payload,
      cartButton: true,
      cartAniButton: false
    };
  }
  if (action.type === GET_CART_PRODUCTS_ERROR) {
    return {
      ...state,
      cart_product_loading: false,
      cart_product_error: true
    };
  }
  if (action.type === HIDE_BUTTON) {
    return {
      ...state,
      cartButton: false,
      cartAniButton: true
    };
  }

  if (action.type === REMOVE_CART) {
    return {
      ...state,
      cartProduct: action.payload
    };
  }
  if (action.type === MODAL_OPEN) {
    return {
      ...state,
      isModalOpen: action.payload
    };
  }
  if (action.type === DEFAULT_STATE) {
    return {
      ...state,
      cartButton: true,
      cartAniButton: false
    };
  }

  //end

  throw new Error(`No Matching "${action.type}" - action type`);
};

export default Cart_Reducers;
