import axios from "axios";
import React, { useContext, useEffect, useReducer } from "react";
import reducer from "../reducers/Cart_Reducers";
import { cart_product_url as url } from "../utils/constants";

import {
  GET_CART_PRODUCTS_BEGIN,
  GET_CART_PRODUCTS_SUCCESS,
  GET_CART_PRODUCTS_ERROR,
  REMOVE_CART,
  HIDE_BUTTON,
  DEFAULT_STATE,
  MODAL_OPEN,
  SEARCH_CART
} from "../actions";

const initialState = {
  cart_product_loading: false,
  cart_product_error: false,
  cartProduct: [],
  cartButton: true,
  cartAniButton: false,
  isModalOpen: false,
};

const CartProductsContext = React.createContext();

export const CartProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const [search,useSearch]=React.useState('');


//console.log(search)
  const fetchCartProducts = async (url) => {
    dispatch({ type: GET_CART_PRODUCTS_BEGIN });
    try {
      const response = await axios.get(url);

      const cartproducts = response.data.Cartlist;
      console.log(cartproducts);
      dispatch({ type: GET_CART_PRODUCTS_SUCCESS, payload: cartproducts });
    } catch (error) {
      dispatch({ type: GET_CART_PRODUCTS_ERROR });
    }
  };

  useEffect(() => {
    fetchCartProducts(url);
  }, []);

  const handleCart = (product) => {
    //dispatch({ type: POST_CART, payload: product });
    dispatch({ type: HIDE_BUTTON });
    const { name, description, price, image, qty, Productid } = product;
    axios({
      url: "https://shrouded-coast-86509.herokuapp.com/Cartdetails",
      method: "POST",
      headers: { "Content-Type": "application/json" },
      data: {
        name,
        description,
        price,
        image,
        keyid: product._id,
        qty,
        Userid: "12345678",
        Productid
      }
    })
      .then((res) => {
        //console.log(res.data.isAuthenticated);
        if (res.data.isAuthenticated) {
          dispatch({
            type: GET_CART_PRODUCTS_SUCCESS,
            payload: [...state.cartProduct, res.data.user]
          });
        } else {
          dispatch({ type: DEFAULT_STATE });
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const ModalOpen = (item) => {
    sessionStorage.setItem("ID", item);
    dispatch({ type: MODAL_OPEN, payload: true });
  };
  const ModalClose = () => {
    dispatch({ type: MODAL_OPEN, payload: false });
    sessionStorage.clear();
  };
  const deleteCart = () => {
    const id = sessionStorage.getItem("ID");
    axios({
      url: `https://shrouded-coast-86509.herokuapp.com/Cartdetails/${id}`,
      method: "DELETE",
      headers: { "Content-Type": "application/json" }
    })
      .then((responce) => {
        if (responce.status === 200) {
          const tempCart = state.cartProduct.filter((item) => item._id !== id);
          dispatch({ type: REMOVE_CART, payload: tempCart });
          dispatch({ type: MODAL_OPEN, payload: false });
          sessionStorage.clear();
        } else {
          dispatch({ type: GET_CART_PRODUCTS_ERROR });
        }
      })
      .catch();
  };

  //search value getting & sending to cart component.
  const refContiner = React.useRef('');
 
  const SearchCart =()=>{
    
     const search=refContiner.current.value;
      useSearch(search);
      
 
  }


  return (
    <CartProductsContext.Provider
      value={{ ...state, handleCart, deleteCart, ModalOpen, ModalClose,refContiner,SearchCart,search }}
    >
      {children}
    </CartProductsContext.Provider>
  );
};
// make sure use
export const useCartProductsContext = () => {
  return useContext(CartProductsContext);
};
